import React, { Component } from "react";

import DeleteIcon from '@material-ui/icons/Delete';
import Fab from '@material-ui/core/Fab';
import FormGroup from '@material-ui/core/FormGroup';
import FormLabel from '@material-ui/core/FormLabel';
import AddIcon from '@material-ui/icons/Add';
import Grid from '@material-ui/core/Grid';

import TextField from '@material-ui/core/TextField';
import NormalInput from "./NormalInput"

import {
    SortableContainer,
    SortableElement,
    SortableHandle
} from "react-sortable-hoc";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import DragHandleIcon from "@material-ui/icons/DragHandle";

function Alapanyag(props) {
    return (
        <FormGroup row={true}>
            <NormalInput label="Mennyiség"
                placeholder="Add meg a mennyiséget..."
                required={false}
                width={props.width}
                margin={props.margin}
                value={props.mennyiseg}
                onChange={props.changeMennyiseg}
                type="number"
            />
            <NormalInput label="Mértékegység"
                placeholder="Add meg a mértékegységet..."
                required={true}
                width={props.width}
                margin={props.margin}
                value={props.mertekegyseg}
                onChange={props.changeMertekegyseg}
                type="text"
            />
            <NormalInput label="Alapanyag"
                placeholder="Add meg az alapanyagot..."
                required={true}
                width={props.width}
                margin={props.margin}
                value={props.alapanyag}
                onChange={props.changeAlapanyag}
                type="text"

            />
            <NormalInput label="Kalória"
                placeholder="Add meg a kalóriát..."
                required={false}
                width={props.width}
                margin={props.margin}
                value={props.kaloria}
                onChange={props.changeKaloria}
                type="number"

            />
            <Fab style={{ marginTop: 20 }} color="secondary" aria-label="Delete" size="small" onClick={props.deleteAlapanyag}>
                <DeleteIcon />
            </Fab>
        </FormGroup>
    );
}

export function getDefaultAlapanyag(nextId) {
    return { id: nextId, name: '', mennyiseg: '', mertekegyseg: '', kaloria: '' };
}
export function getDefaultHozzavalo(nextId) {
    return {
        id: nextId,
        name: "",
        counter: 1,
        alapanyagok: [
            getDefaultAlapanyag(0),
        ]
    };
}

const DragHandle = SortableHandle(() => (
    <ListItemIcon>
        <DragHandleIcon />
    </ListItemIcon>
));

const SortableItem = SortableElement(({ alapanyag, changeMertekegyseg, changeMennyiseg, changeKaloria, changeAlapanyag  }) => {
    return (<ListItem ContainerComponent="div">
        <Alapanyag
            mertekegyseg={alapanyag.mertekegyseg}

            mennyiseg={alapanyag.mennyiseg}
            alapanyag={alapanyag.name}

            kaloria={alapanyag.kaloria}


            width="80%"
            margin="normal"
            key={alapanyag.id}
            changeMertekegyseg={e => changeMertekegyseg(e)}
            changeMennyiseg={e => changeMennyiseg(e)}
            changeKaloria={e => changeKaloria(e)}
            changeAlapanyag={e => changeAlapanyag(e)}
        />

        <ListItemSecondaryAction>
            <DragHandle />
        </ListItemSecondaryAction>
    </ListItem>)
}
);

const SortableListContainer = SortableContainer(({ items, changeMertekegyseg, changeMennyiseg, changeKaloria, changeAlapanyag }) => (
    <List component="div">
        {items.map((alapanyag, index) => {
            return (
                <SortableItem key={index} index={index} alapanyag={alapanyag}
                changeMertekegyseg={e => changeMertekegyseg(e, alapanyag.id)}
                changeMennyiseg={e => changeMennyiseg(e, alapanyag.id)}
                changeKaloria={e => changeKaloria(e, alapanyag.id)}
                changeAlapanyag={e => changeAlapanyag(e, alapanyag.id)}
                    
                    />)
        })}
    </List>
));




class Hozzavalok extends Component {

    constructor(props) {
        super();
        this.state = {
            hozzavalokCounter: 2,
            hozzavalok: [
                {
                    id: 1,
                    name: "",

                }
            ],
        };
    }

    onSortEnd = (oldIndex, newIndex, indexH) => {
        this.props.onSortEnd(oldIndex, newIndex, indexH);
    };
    sortableList = (indexH) => {
        return (
            <SortableListContainer
                items={this.props.hozzavalok[indexH].alapanyagok}
                changeMertekegyseg={(e, id) => this.props.changeMertekegyseg(indexH, id, e)}
                
                changeKaloria={(e, id) => this.props.changeKaloria(indexH, id, e)}                                                
                changeMennyiseg={(e, id) => this.props.changeMennyiseg(indexH, id, e)}
                changeAlapanyag={(e, id) => this.props.changeAlapanyag(indexH, id, e)}
                
                onSortEnd={indexes => this.onSortEnd(indexes.oldIndex, indexes.newIndex, indexH)}
                useDragHandle={true}
                lockAxis="y"
            />
        );
    };


    removeHozzavalo = (index, e) => {
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok.splice(index, 1);
        this.setState({ hozzavalok: hozzavalok });
    }

    addHozzavalo(indexH) {
        const hozzavalok = [...this.state.hozzavalok, getDefaultHozzavalo(this.state.hozzavalokCounter)];
        this.setState({ hozzavalok: hozzavalok, hozzavalokCounter: this.state.hozzavalokCounter + 1 });
    }


    render() {

        return (
            <div>
                <FormLabel >
                    <FormGroup row={true}>

                        <h6 style={{ marginTop: 7 }}>
                            Hozzávalók</h6>
                        <TextField
                            value={this.props.numberOfPeople}
                            style={{ marginBottom: 20, marginLeft: 10, marginRight: 10, width: "10%" }}
                            required={true}
                            onChange={e => this.props.onChangeNumberOfPeople(e)}
                            type="number"
                        />
                        <h6 style={{ marginTop: 7 }}>
                            főre</h6>
                    </FormGroup>
                </FormLabel>
                <ul>
                    {
                        this.props.hozzavalok.map((hozzavalo, indexH) => {
                            const tmp = indexH + 1 + ". hozzavaló csoport"
                            return (
                                <ul key={indexH}>
                                    <NormalInput
                                        label={tmp}
                                        placeholder={tmp}
                                        required={false}
                                        width="100%"
                                        margin="normal"
                                        value={hozzavalo.name}
                                        onChange={(e) => this.props.changeHozzavaloNev(hozzavalo.id, e)}
                                    />

                                    <Fab style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }} color="secondary" aria-label="Delete" size="small" onClick={e => this.props.removeHozzavalo(indexH, e)}>
                                        <DeleteIcon />
                                    </Fab>
                                    <div>
                                        <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                                            <FormLabel>Alapanyagok:</FormLabel>
                                        </Grid>
                                        {this.sortableList(indexH)}
                                        <ul>
                                            {/*
                                                hozzavalo.alapanyagok.map((alapanyag, indexA) => {
                                                    return (
                                                        <div key={indexA}>
                                                            <Alapanyag
                                                                deleteAlapanyag={e => this.props.removeAlapanyag(indexH, indexA, e)}

                                                                changeMertekegyseg={e => this.props.changeMertekegyseg(indexH, alapanyag.id, e)}
                                                                mertekegyseg={alapanyag.mertekegyseg}

                                                                changeMennyiseg={e => this.props.changeMennyiseg(indexH, alapanyag.id, e)}
                                                                mennyiseg={alapanyag.mennyiseg}

                                                                changeAlapanyag={e => this.props.changeAlapanyag(indexH, alapanyag.id, e)}
                                                                alapanyag={alapanyag.name}

                                                                changeKaloria={e => this.props.changeKaloria(indexH, alapanyag.id, e)}
                                                                kaloria={alapanyag.kaloria}


                                                                width="80%"
                                                                margin="normal"
                                                                key={alapanyag.id}
                                                            />

                                                        </div>);
*/
                                                
                                            }

                                        </ul>

                                        <Grid item xs={12} style={{ marginTop: 20 }}>

                                            <Fab
                                                aria-label="Add"
                                                size="small"
                                                color="secondary"
                                                onClick={(e) => this.props.addAlapanyag(indexH, e)}>
                                                <AddIcon />
                                            </Fab>
                                        </Grid>
                                    </div>
                                </ul>
                            )
                        })
                    }
                    <Grid item xs={12} style={{ marginTop: 20 }}>
                        <Fab
                            aria-label="Add"
                            size="small"
                            color="secondary"
                            onClick={() => this.props.addHozzavalo()}
                        >
                            <AddIcon />
                        </Fab>
                    </Grid>
                </ul>
            </div>
        )
    }
}
export default Hozzavalok;